<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Manage Item</span>
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAllManageItem"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
      <b-table
        class="table-sm table-style-3"
        :responsive="true"
        :striped="true"
        :bordered="true"
        :outlined="false"
        :small="false"
        :hover="false"
        :dark="false"
        :fixed="false"
        :foot-clone="false"
        :fields="Outstanding.Header"
        :items="Outstanding.Data"
        @row-clicked="doViewClick"
      >
        <template v-slot:cell(no)="data">{{data.index + 1}}</template>

        <!-- <template
          v-slot:cell(outstanding_amt)="data"
        >{{isCurrency(data.item.outstanding_amt, decimal)}}</template> -->
      </b-table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Outstanding: {
        Header: [
          {
            key: "no",
            label: "No",
            thClass: "HeaderTable",
            tdClass: "ContentTable__Center",
          },
          {
            key: "item_name",
            label: "Item Name",
            thClass: "HeaderTable",
            tdClass: "ContentTable",
          },
          {
            key: "on_fleet_qty",
            label: "On Fleet Qty",
            thClass: "HeaderTable",
            tdClass: "ContentTable",
          },
          {
            key: "waste_qty",
            label: "Waste Qty",
            thClass: "HeaderTable",
            tdClass: "ContentTable",
          },,
          {
            key: "onhand_qty",
            label: "Stock On Hand",
            thClass: "HeaderTable",
            tdClass: "ContentTable",
          },
        ],
        Data: [],
      },
    };
  },
  methods: {
    doViewClick(record, index) {
      var param = record;
      param.isEdit = false;
      param.isView = true;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "MM_SparepartView" });
    },
    doViewAllManageItem() {
      this.$router.push({ name: "MM_Sparepart" });
    },
    getListManageItem() {
      var param = {
        option_url: "/FM/FM_Dashboard",
        line_no: 4,
        user_id: this.getDataUser().user_id,
        portfolio_id: this.getDataUser().portfolio_id,
        subportfolio_id: this.getDataUser().subportfolio_id,
        current_page: 1,
        per_page: 8,
        param_where: "",
        initial_where: "",
        sort_field: "",
        source_field: "",
        param_view: "",
      };
      this.postJSON(this.getUrlList(), param).then((response) => {
        this.Outstanding.Data = response.Data;
      });
    },
  },
  mounted() {
    this.getListManageItem();
  },
};
</script>